@import url("../../estilosGenerales.css");

.menuComponent {
    background: white;
    height: 100vh;
    width: var(--width-sidebar);
    position: fixed;
}
 .nav__logo {
     height: 20%;
     display: flex;
     justify-content: center;
     align-items: center;
     color: var(--color-primary);
 }

 .nav__menu {
     padding: 0;
     margin: 0 auto;
     height: 80%;
     max-height: 38em;
     width: 70%;
     display: flex;
     flex-direction: column;
     justify-content: flex-start;
 }

.nav__menu li {
    list-style: none;
    height: 20%;
}

.menu__link {
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: .5em;
    align-items: center;
    justify-content: center;
    border-radius: 22%;
    text-decoration: none;
    font-weight: 500;
    font-size: 1.15em;
    text-align: center;
    color: var(--color-text-ligth);
    transition: all .06s ease-in-out;
}

.menu--administrar {
    font-size: 1em;
}

.menu__link:hover {
    transform: scale(1.05);
    color: gray;
}

.menuComponent-active {
    background-color: var(--color-primary);
    color: white;
}

.menuComponent-active:hover {
    color: white;
}

.btn-logout__wrapper {
    width: 100%;
    position: absolute;
    bottom: 0.7em;
}

.btn-logout {
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    border: none;
    background-color: transparent;
    font-weight: 500;
    font-size: 1em;
    gap: .5em;
    color: var(--color-text-ligth);
    transition: all .06s ease-in-out;
    
}


.btn-logout:hover {
    transform: scale(1.05);
    color: gray;
}


@media screen and (max-width: 1280px) {
    :root {
        --width-sidebar: 7rem;
    }
    .nav__menu {
        height: 75%;
        width: 75%;
        font-size: 14px;
    }
    .menu__link {
        height: 100%;
        gap: .4em;
    }
}

