.item-carrito * {
  margin: 0;
}

.item-carrito {
  display: flex;
  margin: 2.2em 0;
}

.item-img {
  width: 28%;
  background-color: var(--color-secondary);
  border-radius: 1.4em;
  /* padding: 0.4em; */
  margin-right: 1.2em;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.item-img img {
  width: 130%;
}

.item-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.item-info__header {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0.3em;
}

.item-title {
  font-weight: 500;
  font-size: 1.4em;
  width: 85%;
}

.btn-delete {
  height: 2em;
  width: 2em;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: 2px solid rgb(255, 68, 68);
  border-radius: 0.4rem;
  color: rgb(255, 68, 68);
}

.btn-delete:hover {
  color: white;
  background-color: rgb(255, 68, 68);
  border: none;
}

.item-details {
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.item-details div {
  display: flex;
  align-items: center;
}
/* letra "x" que acompaña a la cantidad */
.item-details div p {
  font-weight: 500;
  font-size: 1.3em;
  margin-right: 0.2em;
}

.item-quantify {
  text-align: center;
  width: 3.5em;
  font-weight: 500;
  font-size: 1.5em;
  border: none;
  outline: none;
}

.item-notes {
  background-color: var(--color-secondary);
  border: none;
  color: var(--color-text-brown);
  font-weight: 700;
  border-radius: 0.6em;
  height: 1.9em;
  padding: 0 1.7em;
}

.item-price {
  font-weight: 500;
  font-size: 1.4em;
  color: gray;
}

@media screen and (max-width: 1380px) {
  .item-carrito {
    margin: 1.2em 0;
  }
  .item-quantify {
    width: 2.2em;
  }
}
@media screen and (max-width: 1024px) {
  .item-carrito {
    margin: 1em 0;
  }
  .item-quantify {
    width: 2.2em;
  }
  .item-img {
    width: 35%;
    border-radius: 1em;
    /* padding: 0.4em; */
    margin-right: 0.8em;
  }
  .item-info__header {
    padding-bottom: 0;
  }

  .item-title {
    font-size: 1.4em;
    line-height: 1.28;
    width: 80%;
  }
  .item-details {
    font-size: 12px;
  }
}
