.pulse-button-pagar {
    animation: pulse-pagar 1.4s infinite;
    box-shadow: 0 0 0 0 rgba(#5a99d4, .5) !important;
}
.pulse-button-pagar:hover {
    animation: none;  
}

@keyframes pulse-pagar {
    0%{
        transform: scale(0.9);
    }
    50%{
        transform: scale(1);
        box-shadow: 0 0 0 20px #f8bec31e;
    }
    100%{
        transform: scale(0.9);
        box-shadow: 0 0 0 0px #f8bec31e;
    }

}