.confirmarOrden {
  height: 100vh;
  position: fixed;
  width: 30vw;
  right: 0;
  background-color: white;
}

.confirmarOrden-header {
  border-bottom: 1px solid rgba(190, 190, 190, 0.568);
  padding-bottom: 1em;
}

.confirmarOrden-title {
  font-weight: 500;
  font-size: 1.8em;
  margin-bottom: 1.5em;
}

.confirmarOrden-body {
  padding-bottom: 2em;
  /* height: 100%; */
  max-height: 22.5em;
  overflow-y: auto;
}

.confirmarOrden-footer {
  background-color: white;
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.confirmarOrden-status {
  border-top: 1px solid rgba(190, 190, 190, 0.568);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 1em;
  background-color: white;
}

.confirmarOrden-status div {
  display: flex;
  gap: 1em;
}

.confirmarOrden-status div button {
  height: 3em;
  padding: 0 0.7em;
  background-color: transparent;
  color: var(--color-primary);
  font-weight: 500;
  font-size: 1.1em;
  border: solid 2px var(--color-primary);
  border-radius: 1em;
}

.active-state {
  background-color: var(--color-primary) !important;
  color: white !important;
}

.confirmarOrden-pago {
  margin-top: 0em;
  border-top: 1px solid rgba(190, 190, 190, 0.568);
  margin-bottom: 2em;
}

.confirmarOrden-precio {
  display: flex;
  justify-content: space-between;
}

.confirmarOrden-pagar {
  display: flex;
  gap: 1em;
  justify-content: center;
  height: 4.5em;
  width: 80%;
  margin: 0 auto;
}

.btn-pagar {
  background-color: var(--color-secondary);
  color: var(--color-primary-light);
  font-weight: 500;
  font-size: 1em;
  width: 7em;
  height: 100%;
  border-radius: 1.2em;
  outline: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.inputACuenta {
  font-size: 1.7em;
  height: 100%;
  width: 40%;
  border-radius: 1em;
}

.btn-pagar-active {
  border: 3px solid var(--color-text-brown);
  color: var(--color-text-brown);
  font-weight: 700;
}

.confirmarOrden-subtitle {
  font-size: 1.4em;
  font-weight: 500;
}

.confirmarOrden-btns-container {
  display: flex;
  gap: 0.8em;
  justify-content: space-between;
  border-top: 1px solid rgba(190, 190, 190, 0.568);
}

.confirmarOrden-btn {
  margin-top: 1em;
  width: 50%;
  height: 3.5em;
  color: var(--color-text-brown);
  font-weight: 700;
  font-size: 1.2em;
  border-radius: 0.85em;
}

.confirmarOrden-btns-container button:nth-child(1) {
  background-color: transparent;
  border: 3px solid var(--color-text-brown);
}
.confirmarOrden-btns-container button:nth-child(2) {
  background-color: var(--color-primary);
  border: none;
  color: white;
}

@media screen and (max-width: 1280px) {
  .confirmarOrden {
    font-size: 14px;
  }
  .confirmarOrden-title {
    margin-bottom: 0.5em;
  }
  .confirmarOrden-body {
    padding-bottom: 2em;
    max-height: 34%;
  }

  .confirmarOrden-btn {
    margin-top: 0.7em;
  }
}
