.plato-component {
    background-color: white;
    border-radius: 4%;
    padding: 0 0 1em;
    text-align: center;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.plato-img img {
    width: 100%;
}

.plato-title {
    margin: 0 0.3em;
    margin-top: 0.5em;
    font-size: 1.5em;
    font-weight: 500;
}

.plato-price {
    font-size: 1.3em;
    font-weight: 700;
}

