.plato-stock {
    color: var(--color-text-ligth);
    font-weight: 500;
    font-size: 1.2em;
}

.agregarplato-btn {
    background-color: var(--color-primary);
    border: none;
    width: 86%;
    color: white;
    font-weight: 500;
    font-size: 1.3em;
    border-radius: 0.8em;
    height: 2.8em;
}

@media screen and (max-width: 1380px) {
    .plato-component {
        font-size: 14px;
    }
    .plato-title {
        margin-top: 0.3em;
        font-size: 1.5em;
        font-weight: 500;
    }
    
    
}

@media screen and (max-width: 1024px) {
    

.plato-title {
    margin-top: 0.2em;
    font-size: 1.4em;
}

.plato-price {
    font-size: 1.2em;
}
.plato-stock {
    font-size: 1.17em;
}

.agregarplato-btn {
    font-size: 1.2em;
    height: 2.5em;
}

}