@import url("../../estilosGenerales.css");

.categoria-component {
    background-color: white;
    border: solid 3px var(--color-secondary);
    border-radius: 22%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 7rem;
    gap: 0.6rem;
    transition: all .06s ease-in-out;
    cursor: pointer;
}

.categoria-component:hover {
    background-color: var(--color-secondary);   
    border: solid 2px var(--color-primary-light);
}

.categoria-component p {
    margin: 0;
    color: var(--color-text-brown);
    font-size: 1.2em;
    font-weight: 400;
}
.categoria-component:hover p {
    font-weight: 500;
}

.categoria-activate {
    font-weight: 500;
    background-color: var(--color-secondary);   
    border: solid 2px var(--color-primary-light);
}

.categoria-activate p {
    font-weight: 500;
}

@media screen and (max-width: 1380px) { 
    .categoria-component {
        border-radius: 18%;
        height: 6em;
        gap: 0.3em;
    }  
}
@media screen and (max-width: 1024px) { 
    .categoria-component {
        border-radius: 15%;
        height: 5.2em;
        gap: 0;
    }  
}