.producto-component {
  background-color: white;
  border-radius: 4%;
  padding: 0 0 1em;
  text-align: center;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.producto-img img {
  width: 100%;
}

.producto-title {
  margin-top: 0.5em;
  font-size: 1.5em;
  font-weight: 500;
}

.producto-price {
  font-size: 1.3em;
  font-weight: 700;
}

