.producto-stock {
  color: var(--color-text-ligth);
  font-weight: 500;
  font-size: 1.2em;
}

.agregarProducto-btn {
  background-color: var(--color-primary);
  border: none;
  width: 86%;
  color: white;
  font-weight: 500;
  font-size: 1.3em;
  border-radius: 0.8em;
  height: 2.8em;
}

@media screen and (max-width: 1280px) {
  .producto-component {
    font-size: 14px;
  }
}
