.most-ordered-item * {
  margin: 0;
  padding: 0;
}

.most-ordered-item {
  margin-bottom: 2.2em;
  list-style: none;
  display: flex;
}


.most-ordered__img {
  width: 23%;
  background-color: var(--color-secondary);
  border-radius: 1.4em;
  /* padding: 0.4em; */
  margin-right: 1.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.most-ordered__img img {
  width: 100%;
}

.most-ordered__name {
  font-size: 1.4em;
  font-weight: 500;
}

.most-ordered__quantify {
  font-size: 1.1em;
  font-weight: 500;
  color: var(--color-text-ligth);

}

@media screen and (max-width: 1380px) {
  .most-ordered-item {
    margin-bottom: 1.5em;
  }
  .most-ordered__img {
    height: 3em;
    width: 30%;
    border-radius: 1em;
    margin-right: 0.9em;
  }

  .most-ordered__name {
    font-size: 1.1em;
    font-weight: 500;
    margin-bottom: 0;
    line-height: 1.3;
  }

  .most-ordered__quantify {
    font-size: 0.98em;
    font-weight: 500;
    color: var(--color-text-ligth);
    line-height: 1.2em;
  } 
}
