:root {
  --color-primary: #704232;
  --color-primary-light: #ae9f9b;
  --color-secondary: #f6efef;
  --color-text-brown: #7f635a;
  --color-text-ligth: #b6b6b6;
  --width-sidebar: 10vw;
  --bg-color: #f9f9fb;

  --border-radius: 22%;
}

body {
  background: var(--bg-color) !important;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: black;
}

.title {
  font-weight: 500;
  font-size: 2.6em;
}

.subtitle {
  font-weight: 500;
  font-size: 2.4rem;
}

/* LOGIN SCREEN */
.login-screen-container {
  width: 100%;
  height: 100vh;
  background-color: white;
}

.form-login-container {
  width: 21em;
}

.form-login-container .form-control {
  font-weight: 400 !important;
  font-size: 1.1em !important;
}

.bg-login-container {
  background: url("./assets/images/bg-kitchen.jpg") no-repeat center;
  background-size: cover;
}

@media screen and (max-width: 1380px) {
  .title {
    font-size: 2.1em;
  }
  .subtitle {
    font-weight: 500;
    font-size: 2em;
  }

  .form-login-container {
      font-size: 14px;
  }
}

@media screen and (max-width: 1024px) {
  .title {
    font-size: 2em;
  }
  .subtitle {
    font-weight: 500;
    font-size: 1.7em;
  }

  .form-login-container {
      font-size: 14px;
  }
}
