.productos__header {
  display: flex;
  justify-content: space-between;
}

.productos__header p {
  font-size: 1.4em;
  font-weight: 500;
  color: var(--color-text-ligth);
}

.productos-container {
  display: grid;
  grid-template-columns: repeat(3, minmax(13em, 1fr));
  gap: 2.5rem;
}

@media screen and (max-width: 1380px) {
  .productos-container {
    gap: 1.2em;
  }
  .productos__header p {
    font-size: 1.3em;
  }
}
@media screen and (max-width: 1024px) {
  .productos-container {
    grid-template-columns: repeat(3, minmax(10em, 1fr));
    gap: 1em;
  }
}
