.perfilCliente {
    background-color: white;
    overflow: hidden;
}

.perfilCliente-name {
    font-weight: 500;
    font-size: 1.1rem;
}

.icon-cliente {
    color: var(--color-text-brown);
}

.btn-delete-perfil-cliente {
    background-color: transparent;
    border: none;
    outline: none;
    height: 2.3rem;
    width: 2.3rem;
    font-size: 1.3rem;
    border-radius: 0.3rem;
}

.btn-delete-perfil-cliente:hover {
    background-color: #E35D6A;
    color: white;
    font-size: 1.5rem;
}