.reporte-pedidos {
    background-color: white;
    border-radius: 3%;
}

.reporte-pedidos__header {
    display: flex;
    justify-content: space-between;
}

.reporte-pedidos__header p {
    font-size: 1.4rem;
    font-weight: 500;
    color: var(--color-text-ligth);
}

.btn-ver-detalle {
    background-color: var(--color-text-brown) !important;
    color: white !important;
    border-radius: 0.4rem;
    display: block !important;
    padding: 0.4rem 0.5rem;
    outline: none;
    border: none;
    font-weight: 500;
}

.btn-ver-detalle:hover {
    background-color: var(--color-primary)!important;
}

.badge-pedido {
    height: 100%;
    padding: .2rem .5rem;
    border-radius: 0.4rem;
    text-align: center;
    font-weight: 500;
}

.badge-pedido.entregado {
    background-color: #3eb59538;
    color: #3eb595;
}

.badge-pedido.pendiente {
    background-color: #ffda6a4f;
    color: #eca200;
}

.badge-pedido.pagado {
    background-color: #3eb59538;
    color: #3eb595;
}

.badge-pedido.debe {
background-color: #d95f6938;
color: #d95f69;
}

