.categorias-container {
    margin-left: 1.5em;
    display: grid;
    grid-template-columns: repeat(auto-fit, 6em);
    gap: 1.3em;
}

@media screen and (max-width: 1380px) {
    .categorias-container {
        font-size: 14px;
        grid-template-columns: repeat(auto-fit, 5.5em);
    }
    
}
@media screen and (max-width: 1025px) {
    .categorias-container {
        font-size: 14px;
        grid-template-columns: repeat(auto-fit, 5em);
    }
    
}