.lista-clientes {
    background-color: white;
    border-radius: 3%;
}

.lista-clientes__header {
    display: flex;
    justify-content: space-between;
}

.lista-clientes__header p {
    font-size: 1.4rem;
    font-weight: 500;
    color: var(--color-text-ligth);
}

.btn-table-secondary {
    border: solid 2px var(--color-text-brown) !important;
    background: white !important;
    color: var(--color-text-brown) !important;
    font-weight: 500 !important;
}

.btn-table-secondary:hover {
    background: var(--color-text-brown) !important;
    color: white !important;
    border: none;
}

.btn-table-brown {
    border: solid 2px var(--color-text-brown) !important;
    background: var(--color-text-brown) !important;
    color: white !important;
    font-weight: 500;
}

.btn-table-brown:hover {
    background: var(--color-primary) !important;
}