.cliente-search {
    height: 100%;
}

.input-cliente-search {
    width: 100%;
    display: block;
    height: 2.2em;
    font-size: 1.1rem;
    border: none;
    outline: none;
}

.lista-clientes-search {
    position: absolute;
    width: 100%;
    top: 100%;
}

.cliente-search-item {
    border-bottom: 1px solid rgba(128, 128, 128, 0.151);
    font-weight: 500;
    font-size: 1.05rem;
    background-color: white;
}