.carrito-component * {
  padding: 0;
  margin: 0;
}

.carrito-component {
  height: 100vh;
  position: fixed;
  width: 30vw;
  right: 0;
  background-color: white;
  display: flex;
  flex-direction: column;
}

.carrito-header {
  border-bottom: 1px solid rgba(190, 190, 190, 0.568);
  height: 25%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.carrito-title {
  font-weight: 500;
  font-size: 1.8em;
  margin-bottom: 1.5em;
}

.carrito-info {
  display: flex;
  justify-content: space-between;
  font-size: 1.5em;
  font-weight: 500;
}

.carrito-body {
  padding-right: 1em;
  padding-bottom: 2em;
  height: 65%;
  overflow-y: auto;
}

.carrito-footer {
  flex: 10%;
  /* position: absolute; */
  width: 100%;
  bottom: 0;
  /* max-height: 10em; */
  left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.carrito-total {
  border-top: 1px solid rgba(190, 190, 190, 0.568);
  display: flex;
  justify-content: space-between;
}

.carrito-total p {
  font-size: 1.5em;
  font-weight: 500;
}

.btn-continuar-orden {
  width: 100%;
  background-color: var(--color-primary);
  color: white;
  height: 3em;
  border: none;
  border-radius: 1em;
  font-weight: 500;
  font-size: 1.4em;
}


.btn-continuar-orden--disactived {
  background-color: var(--color-primary-light);
}

@media screen and (max-width: 1280px) {
    .carrito-component {
        font-size: 14px;
    }
    .carrito-body {
        padding-bottom: 2em;
        height: 55%;
      }
    .carrito-footer {
    height: 22vh;
    font-size: 15px;
  }

  .btn-continuar-orden {
    height: 2.8em;
  } 
}

