.most-common-component {
    background-color: white;
    border-radius: 1em;
}

.most-common__header h4 {
    margin-bottom: 1em;
    padding-bottom: 1em;
    border-bottom: 1px solid rgba(190, 190, 190, 0.568);
}

@media screen and (max-width: 1280px) {
    .most-common-component {
       border-radius: 0.8em;
    }
    
    .most-common__header h4 {
        padding-bottom: .5em;
        margin-bottom: 0.5em;
        border-bottom: 1px solid rgba(190, 190, 190, 0.568);
        font-size: 1.3em;
    }
    
}

