.settings-menu * {
    margin: 0;
    padding: 0;
}

.settings-menu {
    background-color: white;
    overflow: hidden;
    border-radius: 1em;
}


.settings-menu__link {
    color: var(--color-text-ligth);
    padding: 2.4em;
    text-decoration: none;
    display: flex;
    transition: all .06s ease-in-out;
}


.settings-menu__link:hover {
    color: gray;
}

.settings-menu-active {
    background-color: var(--color-primary);
    color: white;
}

.settings-menu-active:hover {
    color: white;
}

.settings-menu__icon {
    margin-right: 0.8em;
}

.settings-menu__title {
    font-size: 1.3em;
    font-weight: 500;
}

.settings-menu__subtitle {
    font-size: 1em;
    font-weight: 400;
}

@media screen and (max-width: 1380px) {
    .settings-menu {
        font-size: 14px;
    }

    .settings-menu__link {
        padding: 1.8em 2em;
    }
}

@media screen and (max-width: 1024px) {

    .settings-menu__link {
        padding: 1em 1em;
    }
    
}