.dato-estadistico * {
    margin: 0;
    padding: 0;
}

.dato-estadistico {
    background-color: white;
    border: solid 3px white;
    color: black;
    border: solid 4px var(--color-secondary);
    border-radius: 1.5em;
    padding: 1em;
}

.dato-icon {
    color: white;
    width: 2.8em;
    height: 2.8em;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.6em;  
}

.dato-number {
    font-weight: 700;
    font-size: 2em;
}

.dato-info {
    font-weight: 500;
    font-size: 1.1em;
    color: var(--color-text-ligth);
}

.bg-ventas {
    background-color: #20c997;
}

.bg-cobrar {
    background-color: #EA868F;
}

.bg-pedidos {
    background-color: #FFDA6A;
}

@media screen and (max-width: 1380px) {
    .dato-estadistico {
        font-size: 14px;
        display: flex;
        justify-content: space-between;
    }   
    .dato-estadistico div {
        text-align: start;
        order: -1;
    }
    .dato-number {
        font-weight: 500;
        font-size: 2em;
    }
}
@media screen and (max-width: 1024px) {
    .dato-estadistico {
        border: solid 3px var(--color-secondary);
        border-radius: 1em;
        padding: 0.8em;
    }
    .dato-number {
        font-size: 1.8em;
    }
    .dato-info {
        font-size: 1.05em;
    }
}
