.administrar-productos {
  background-color: white;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* HEADER */
.administrar-productos__header {
  display: flex;
  justify-content: space-between;
}

.btn-administrar-categorias {
  color: var(--color-text-brown);
  font-weight: 500;
  font-size: 1.1em;
  border-radius: 0.6em;
  background-color: transparent;
  border: 2px solid var(--color-text-brown);
  padding: 0.5em;
  transition: all 0.1s ease;
}

.btn-administrar-categorias:hover {
  background-color: var(--color-text-brown);
  color: white;
}

/* NAVEGACIÓN */
.administrar-productos__nav ul {
  width: 100%;
  border-bottom: 2px solid rgba(141, 141, 141, 0.144);
}

.administrar-productos__link {
  padding: 0 0.3em;
  display: block;
  display: flex;
  align-items: center;
  text-decoration: none;
  color: black;
  font-weight: 500;
  font-size: 1.2em;
  cursor: pointer;
}

.administrar-productos__link:hover {
  color: var(--color-text-brown);
  border-bottom: 4px solid brown;
  margin-bottom: -4px;
  z-index: 2;
}
.administrar-productos__link--activate {
  color: var(--color-text-brown);
  border-bottom: 4px solid brown;
  margin-bottom: -4px;
  z-index: 2;
}

/* BODY */
.administrar-productos__container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
}

.crear-plato-btn {
  text-align: center;
    border: 3px dashed rgb(236, 132, 132);
    border-radius: 4%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.4em;
    font-weight: 700;
    cursor: pointer;
    color: rgb(235, 116, 116);
    min-height: 12em;
}

.sin-platos-btn-size {
    width: 15em;
    height: 20em;

}

@media screen and (max-width: 1380px) {
  .administrar-productos {
    font-size: 15px;
  }
  .administrar-productos__container {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.3em;
  }
}
@media screen and (max-width: 1024px) {
  .administrar-productos__container {
    gap: 1em;
  }
}